.accordion {
  margin: 0;
  padding: 0;
  list-style: none;
}
.accordion .accordion-item {
  border-radius: 1rem;
  transition: background-color 200ms ease;
}
.accordion .accordion-item.expanded {
  background-color: hsl(255, 100%, 60%);
}
.accordion .accordion-item.expanded .label .text {
  font-weight: 600;
}
.accordion .accordion-item.expanded .label:hover .icon,
.accordion .accordion-item.expanded .label:focus-visible .icon {
  color: hsl(255, 100%, 100%);
}
.accordion .accordion-item.label-only .label .icon, .accordion .accordion-item.label-only .label:hover .icon, .accordion .accordion-item.label-only .label:focus-visible .icon {
  color: #3d3946;
}
.accordion .accordion-item .label {
  display: flex;
  justify-content: space-between;
  gap: 0.75rem;
  width: 100%;
  padding: 1rem;
  color: hsl(255, 100%, 100%);
  background-color: transparent;
  text-align: left;
}
.accordion .accordion-item .label:hover, .accordion .accordion-item .label:focus-visible {
  cursor: pointer;
}
.accordion .accordion-item .label:hover .icon, .accordion .accordion-item .label:focus-visible .icon {
  color: hsl(255, 100%, 60%);
}
.accordion .accordion-item .label .text {
  margin: 0;
  font-weight: 300;
  transition: font-weight 200ms ease;
}
.accordion .accordion-item .label .icon {
  flex-shrink: 0;
  display: block;
  width: 1.3rem;
  height: 1.3rem;
  transition: color 200ms ease;
}
.accordion .accordion-item .label .icon.hidden {
  display: none;
}
.accordion .accordion-item .body {
  height: 0;
  margin: 0;
  overflow: hidden;
  transition: height 200ms ease;
}
.accordion .accordion-item .body .text {
  margin: 0;
  padding: 0 1rem 1rem;
}
.accordion .accordion-item .body .text a {
  color: hsl(30, 100%, 65%);
}

a {
  color: #8c66ff;
  text-decoration: none;
}

a.button {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem 1.5rem;
  color: hsl(255, 100%, 100%);
  border: 0.125rem solid hsl(255, 100%, 100%);
  border-radius: 1000000px;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 1.25rem;
  transition: color 200ms ease, background-color 200ms ease;
}
a.button:hover, a.button:focus-visible {
  color: hsl(255, 100%, 0%);
  background-color: hsl(255, 100%, 100%);
}
a.button .text {
  margin: 0;
}
a.button .icon {
  display: block;
  width: 1.3rem;
  height: 1.3rem;
}

button {
  padding: 0.75rem 1.5rem;
  color: hsl(255, 100%, 0%);
  background-color: hsl(255, 100%, 100%);
  font-family: inherit;
  font-size: inherit;
  border: none;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

html {
  height: 100%;
  font-family: Inter, sans-serif;
  font-size: 14px;
  font-weight: 300;
  overflow: hidden;
}
@media only screen and (min-width: 600px) {
  html {
    font-size: 16px;
  }
}
@media only screen and (min-width: 800px) {
  html {
    font-size: 18px;
  }
}
@media only screen and (min-width: 1400px) {
  html {
    font-size: 20px;
  }
}

body {
  height: 100%;
  margin: 0;
  color: hsl(255, 100%, 100%);
  background-color: hsl(240, 30%, 5%);
  overflow: hidden scroll;
  scroll-behavior: smooth;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
}

footer {
  padding: 5rem;
}

section {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 3rem 1rem;
}
@media only screen and (min-width: 600px) {
  section {
    padding: 3rem;
  }
}
@media only screen and (min-width: 1400px) {
  section {
    padding: 5rem;
  }
}

.section-title {
  margin: 0 auto 3rem;
  text-transform: uppercase;
  font-size: 1.5rem;
  font-weight: 600;
}
@media only screen and (min-width: 1400px) {
  .section-title {
    margin-bottom: 5rem;
  }
}
.section-title::after {
  content: "";
  display: block;
  width: 100%;
  height: 0.2rem;
  margin-top: 0.75rem;
  background-color: hsl(255, 100%, 60%);
}

header {
  position: relative;
  height: 100%;
  min-height: stretch;
  overflow: hidden;
  z-index: -2;
}
header .background-image {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.4;
  z-index: -1;
}
header .container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}
header .title,
header .description {
  margin: 0;
  text-align: center;
}
header .title {
  color: hsl(30, 100%, 65%);
  text-shadow: 0 0.2rem 1rem rgba(0, 0, 0, 0.2);
  font-family: Rubik, sans-serif;
  font-size: 3.5rem;
  font-weight: 600;
}
header .description {
  max-width: 300px;
  margin-top: 2rem;
  text-shadow: 0 0.2rem 1rem rgba(0, 0, 0, 0.5);
  line-height: 1.5;
  font-size: 1.5rem;
}
header .cta {
  margin-top: 6rem;
  backdrop-filter: blur(4px);
  box-shadow: 0 0.2rem 1rem 0 rgba(0, 0, 0, 0.5);
}
@media only screen and (min-width: 600px) {
  header .title {
    font-size: 5rem;
  }
  header .description {
    max-width: 580px;
  }
}
@media only screen and (min-width: 1400px) {
  header .title {
    font-size: 6rem;
  }
  header .description {
    max-width: 640px;
  }
}

.introductions .introduction {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column-reverse;
  width: 100%;
  margin-top: 1rem;
  background-color: #1f1f2e;
  border-radius: 1rem;
}
.introductions .introduction:first-of-type {
  margin-top: 0;
}
.introductions .introduction .description {
  position: relative;
  width: 100%;
  margin: auto 0;
  padding: 1.5rem;
  line-height: 1.5;
}
.introductions .introduction .image {
  width: 100%;
  height: auto;
}
.introductions .introduction .image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media only screen and (min-width: 800px) {
  .introductions .introduction {
    flex-direction: row;
    gap: 4rem;
    padding: 1rem;
    background-color: transparent;
  }
  .introductions .introduction:nth-child(2n) {
    flex-direction: row-reverse;
  }
  .introductions .introduction .description {
    position: relative;
    width: 50%;
    margin: auto 0;
    padding: 2rem 0 2rem 2rem;
    line-height: 1.5;
  }
  .introductions .introduction .description::before {
    content: "";
    display: block;
    position: absolute;
    top: 2rem;
    bottom: 2rem;
    left: 0;
    width: 0.2rem;
    background-color: hsl(255, 100%, 60%);
  }
  .introductions .introduction .image {
    width: 50%;
  }
  .introductions .introduction .image img {
    border-radius: 1rem;
  }
}

.rules .rules-container {
  width: 100%;
  max-width: 640px;
  margin: 0;
  padding: 0;
  list-style: none;
}
.rules .rules-closing {
  width: 100%;
  max-width: 640px;
  margin-top: 2rem;
  padding: 0 1rem;
  line-height: 1.5;
}

.applications-text {
  max-width: 640px;
  text-align: center;
}

footer .copyright {
  text-align: center;
}